<template>
  <div class="col-md-12">
    <div class="card card-container">
      <img
        id="profile-img"
        src="https://img.icons8.com/ios/50/000000/ring-side-view.png"
        class="profile-img-card"
      />
      <h4 style="text-align: center">ARAREAL</h4>
      <form name="form" @submit.prevent="handleLogin">
        <div class="form-group">
          <label for="username">Username</label>
          <input
            v-model="user.username"
            type="text"
            class="form-control"
            name="username"
            required
          />
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input
            v-model="user.password"
            type="password"
            class="form-control"
            name="password"
            required
          />
        </div>

        <div class="form-group">
          <button class="btn btn-primary btn-block" :disabled="loading">
            <span
              v-show="loading"
              class="spinner-border spinner-border-sm"
            ></span>
            <span>Login</span>
          </button>
        </div>

        <div class="form-group">
          <div v-if="message" class="alert alert-danger" role="alert">
            {{ message }}
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import AuthService from "@/services/AuthService";
import ResponseData from "@/types/ResponseData";
import { Md5 } from "md5-typescript";

export default defineComponent({
  name: "Login",
  data() {
    return {
      message: "",
      loading: false,
      user: {
        username: "",
        password: "",
      },
    };
  },
  computed: {
    loggedIn() {
      var token = localStorage.getItem("user");
      // check token
      return token;
    },
  },
  created() {
    if (this.loggedIn as string) {
      this.$router.push("/products");
    }
  },
  methods: {
    handleLogin(user: any) {
      this.loading = true;
      //   hash password
      let hashPassword = Md5.init(this.user.password);

      AuthService.login(this.user.username as string, hashPassword as string)
        .then((response: ResponseData) => {
          localStorage.setItem("user", JSON.stringify(response.data));
          this.loading = false;
          window.location.reload();
          this.$router.push("/products");
        })
        .catch((e: Error) => {
          console.log(this.user.username);
          console.log(hashPassword);
          console.log(e);
          this.loading = false;
          this.message = "Username or password wrong!";
        });
    },
  },
});
</script>
<style>
label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 60px;
  height: 60px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.error-feedback {
  color: red;
}
</style>
